import styled, { css } from "styled-components";

export interface IconButtonProps {
  children: React.ReactNode;
  active?: boolean;
  /**
   * @default #000
   * @description default color
   */
  defaultColor?: string;
  /**
   * @default #000
   * @description color when hovered
   */
  hoverColor?: string;
  /**
   * @default #000
   * @description color when active
   */
  activeColor?: string;
  /**
   * @default defaultColor
   * @description color when disabled
   */
  disabledColor?: string;
  /**
   * @default transparent
   * @description default background color
   */
  defaultBGColor?: string;
  /**
   * @default baseColor.Blue1 #DCEBF3
   * @description background color when hovered
   */
  hoverBGColor?: string;
  /**
   * @default brandColor.Primary7 #457D96
   * @description background color when active
   */
  activeBGColor?: string;
  /**
   * @default defaultBGColor
   * @description background color when disabled
   */
  disabledBGColor?: string;
  /**
   * @default defaultBGColor
   * @description default border color
   */
  defaultBorderColor?: string;
  /**
   * @default hoverBGColor baseColor.Blue1 #DCEBF3
   * @description background color when hovered
   */
  hoverBorderColor?: string;
  /**
   * @default activeBGColor brandColor.Primary7 #457D96
   * @description background color when active
   */
  activeBorderColor?: string;
  /**
   * @default defaultBGColor
   * @description background color when disabled
   */
  disabledBorderColor?: string;
}

/**
 * @example
 * import styled from "styled-components";
 * import { DmIcon12Down } from "@orthogonalgroup/ui/src/icons";
 * import IconButton from "@orthogonalgroup/ui/src/IconButtons";
 *
 * const Arrow = styled(DmIcon12Down)`
 * transition: all 0.2s;
 *
 * ${IconButton}:hover:not(:disabled) & {
 *   transform: rotate(180deg);
 * }
 * `;
 *
 * <IconButton>
 *   <Arrow fill="currentColor" width={20} height={20} />
 * </IconButton>
 *
 **/
const IconButton = styled.button<IconButtonProps>`
  border: none;
  outline: none;
  box-sizing: border-box;
  ${(p) => p.theme.flexCSS.row}
  ${(p) => p.theme.flexCSS.center}
  border-radius: 4px;
  border-width: 1px;
  border-style: solid;
  padding: 3.6px;
  cursor: ${(p) => (p.disabled ? "not-allowed" : "pointer")};

  ${({ theme: { colors }, active, disabled, ...restColors }) => {
    // normal styles
    let color = restColors.defaultColor || "#000";
    let _hoverColor = restColors.hoverColor || "#000";
    let backgroundColor = restColors.defaultBGColor || "transparent";
    let borderColor = restColors.defaultBorderColor || backgroundColor;
    let hoverBackgroundColor =
      restColors.hoverBGColor || colors.baseColor.Blue1;
    // active styles
    if (active && !disabled) {
      color = restColors.activeColor || color;
      _hoverColor = restColors.activeColor || _hoverColor;
      backgroundColor = restColors.activeBGColor || colors.brandColor.Primary7;
      borderColor = restColors.activeBorderColor || backgroundColor;
      hoverBackgroundColor =
        restColors.activeBGColor || colors.brandColor.Primary7;
    }
    // disable styles
    if (disabled) {
      color = restColors.disabledColor || color;
      borderColor = restColors.disabledBorderColor || backgroundColor;
      backgroundColor = restColors.disabledBGColor || backgroundColor;
    }
    return css`
      color: ${color};
      border-color: ${borderColor};
      background-color: ${backgroundColor};
      transition: all 0.2s;

      &:hover:not(:disabled) {
        color: ${_hoverColor};
        border-color: ${restColors.hoverBorderColor || hoverBackgroundColor};
        background-color: ${hoverBackgroundColor};
      }
    `;
  }}
`;
export default IconButton;
